.pg-home {
    width: 100%;
    min-height: 100vh;
    overflow-y: auto;
}

.register-modal {
    width: 1000px;
    height: 800px;
}

.register-modal-fixed {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    overflow-y: auto;
    justify-content: center;
}

.register-content-modal {
    width: 1000px;
    height: 700px;
    position: relative;
}

.register-close-modal {
    position: absolute;
    width: 24px;
    top: -30px;
    right: -30px;
    cursor: pointer;
}

.open-register-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.open-register-modal-button {
    border: 1px solid #005ad9;
    padding: 20px 40px;
    background-color: transparent;
    color: #005ad9;
    font-size: 18px;
    cursor: pointer;
}